@import '../../../../assets/styles/sass/abstracts/variables.scss';

.section {
    min-height: 100vh;
    width: 100%;
    position: relative;
    overflow: hidden;

    &BgBox {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        // pointer-events: none;
    }

    &BgImg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &Container {
        padding-top: 10rem;

        @media (max-width:1200px) {
            padding-top: 8rem;
        }

        @media (max-width:600px) {
            padding-top: 6rem;
        }

        @media (max-width:500px) {
            padding-top: 4rem;
        }
    }

    .globalContainer {
        @extend %globalContainer;
    }

    &Head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 3rem;

        @media (max-width:800px) {
            margin-bottom: 2rem;
            ;
        }

        @media (max-width:500px) {
            flex-direction: column;
            align-items: flex-start;
        }

    }

    &Title {
        @extend %title;

        @media (max-width:500px) {
            margin-bottom: 1rem;
        }

    }

}